<template>
  <div id="class-template">
    <div class="main">
      <div class="top">
        <div class="tt">班级模板</div>
        <div class="btns">
          <el-button type="primary" size="small" @click="courseAdd">创建新课程</el-button>
          <el-button type="primary" size="small" @click="templateAdd">创建新模板</el-button>
          <el-button type="danger" size="small" @click="deleteData">删除</el-button>
        </div>
      </div>
      <div class="content">
        <div class="detail">
          <div
            class="item"
            :class="index===selectIndex?'selected':''"
            v-for="(item, index) in dataList"
            :key="index"
            @click="selectTemplate(item, index)"
          >
            <div class="c-n">课程：{{item.courseName}}</div>
            <div class="c-num">
              <span>人数</span>
              <span>{{item.personNum}}</span>
            </div>
            <div class="c-class">
              <span>班级</span>
              <span>{{item.className}}</span>
            </div>
          </div>
        </div>
        <div>
          <el-button type="primary" size="small" @click="preStep">上一步</el-button>

          <el-button type="primary" size="small" @click="nextStep">下一步</el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import { formatDate, getWeek } from "@/utils/utils";
import list from "@/mixin/list";
export default {
  components: {},
  mixins: [list],
  props: {},
  data() {
    return {
      selectIndex: -1
    };
  },
  computed: {},
  watch: {},
  created() {
    this.searchData();
  },
  methods: {
    searchData() {
      this.dataList = [];
      this.selectIndex = -1;
      this.$http
        .get(this.$api.CourseTemplateList, {
          userID: this.$http.getUser().UserID
        })
        .then(res => {
          if (res.Result && res.Datas && res.Datas.length > 0) {
            res.Datas.forEach(item => {
              this.dataList.push({
                courseName: item["CourseName"],
                personNum: item["ClassNumber"],
                className: item["ClassName"],
                classId: item["ClassID"],
                templateId: item["CTID"],
                state: item["State"]
              });
            });
          }
        })
        .catch(err => {});
    },
    selectTemplate(item, index) {
      this.selectIndex = index;
    },
    deleteData() {
      if (this.selectIndex < 0) {
        this.$message({
          showClose: true,
          message: "请选择课程模板",
          type: "error"
        });
        return;
      }

      this.$confirm("此操作将删除该模板, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$http
            .post(this.$api.CourseTemplateDelete, {
              ids: this.dataList[this.selectIndex].templateId
            })
            .then(res => {
              if (res.Result) {
                this.$message({
                  message: "删除成功！",
                  type: "success"
                });
                setTimeout(() => {
                  this.searchData();
                }, 100);
              }
            })
            .catch(err => {
              this.$message({
                showClose: true,
                message: "删除失败",
                type: "error"
              });
            });
        })
        .catch(() => {});
    },
    courseAdd() {
      this.$router.push("/main/teacherSpace/prepare/createCourse");
    },
    templateAdd() {
      this.$router.push("/main/teacherSpace/prepare/newCsTemp");
    },
    preStep() {
      this.$router.push("/main/teacherSpace/prepare/courseView");
    },
    nextStep() {
      if (this.selectIndex > -1) {
        // 以下方法避免特殊字符的影响
        this.$router.push({
          path:"/main/teacherSpace/prepare/createCourse",
          query:{
            courseId:"0",
            classId:this.dataList[this.selectIndex].classId,
            courseName: this.dataList[this.selectIndex].courseName
          }
        });
      } else {
        this.$router.push("/main/teacherSpace/prepare/createCourse");
      }
    }
  }
};
</script>

<style lang='less'>
#class-template {
  .main {
    height: 100%;
    background: #fff;
    padding: 20px;
    .top {
      display: flex;
      justify-content: space-between;
      margin-bottom: 30px;
      .tt {
        font-size: 14px;
        font-family: MicrosoftYaHei;
        font-weight: 400;
        color: rgba(51, 51, 51, 1);
      }
    }

    .content {
      .detail {
        display: flex;
        justify-content: space-evenly;
        flex-wrap: wrap;

        .item {
          padding: 20px;
          text-align: left;
          width: 206px;
          /*height: 120px;*/
          background: rgba(241, 248, 255, 0.8);
          // opacity: 0.8;
          border-radius: 3px;
          margin-bottom: 30px;
          .c-n {
            font-size: 16px;
            font-family: MicrosoftYaHei;
            font-weight: 400;
            color: rgba(51, 51, 51, 1);
            // line-height: 2.8;
            margin-bottom: 15px;
          }
          .c-num,
          .c-class {
            display: flex;
            justify-content: space-between;
            font-size: 14px;
            line-height: 2;
            color: #666;
          }
        }
        .selected {
          background-color: #409eff;
          border-color: #409eff;
          .c-n,
          .c-num,
          .c-class {
            color: #fff;
          }
        }
      }
      // .ft {
      //   width: 170px;
      //   margin: 0 auto;
      //   margin-top: 20px;
      //   button {
      //     width: 60px;
      //     height: 24px;
      //     background: rgba(42, 158, 252, 1);
      //     border-radius: 3px;
      //     font-size: 12px;
      //     border: none;
      //     color: #fff;
      //     margin-right: 20px;
      //   }
      // }
    }
  }
}
</style>



